* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  margin: 0 auto;
}
.container .row {
  display: flex;
  flex-wrap: wrap;
  padding: 1em 0;
}
.container .row [class*=col-] {
  min-height: 1px;
  width: 8.3333333333%;
  flex-wrap: wrap;
  padding: 1em;
}
.container .row .col-1 {
  width: 8.3333333333%;
}
.container .row .col-2 {
  width: 16.6666666667%;
}
.container .row .col-3 {
  width: 25%;
}
.container .row .col-4 {
  width: 33.3333333333%;
}
.container .row .col-5 {
  width: 41.6666666667%;
}
.container .row .col-6 {
  width: 50%;
}
.container .row .col-7 {
  width: 58.3333333333%;
}
.container .row .col-8 {
  width: 66.6666666667%;
}
.container .row .col-9 {
  width: 75%;
}
.container .row .col-10 {
  width: 83.3333333333%;
}
.container .row .col-11 {
  width: 91.6666666667%;
}
.container .row .col-12 {
  width: 100%;
}
@media (max-width: 550px) {
  .container .row [class*=col-] {
    width: 100%;
  }
}

header {
  background-color: mediumpurple;
  height: 200px;
}

footer {
  background-color: blue;
  height: 200px;
}

aside .right-sidebar {
  background-color: blue;
  height: 500px;
}
aside main {
  background-color: yellow;
  height: 500px;
}